* {
  box-sizing: border-box;
}

.wrapper {
  max-width: 50rem;
  width: 100%;
  margin: 0 auto;
}

.tabs {
  height: 14.75rem;
  background: #1abc9c;
  margin: 3rem 0;
  position: relative;
}

.tabs:before, .tabs:after {
  content: "";
  display: table;
}

.tabs:after {
  clear: both;
}

.tab {
  float: left;
}

.tab-switch {
  display: none;
}

.tab-label {
  height: 3em;
  color: #fff;
  cursor: pointer;
  background: #1abc9c;
  border-right: .125rem solid #16a085;
  padding: 0 1.618em;
  line-height: 2.75em;
  transition: all .25s;
  display: block;
  position: relative;
  top: 0;
}

.tab-label:hover {
  transition: top .25s;
  top: -.25rem;
}

.tab-content {
  z-index: 1;
  color: #2c3e50;
  opacity: 0;
  background: #fff;
  border-bottom: .25rem solid #bdc3c7;
  padding: 1.618rem;
  transition: all .35s;
  position: absolute;
  top: 2.75em;
  left: 0;
}

.tab-switch:checked + .tab-label {
  color: #2c3e50;
  z-index: 1;
  background: #fff;
  border-bottom: 0;
  border-right: .125rem solid #fff;
  transition: all .35s;
  top: -.0625rem;
}

.tab-switch:checked + label + .tab-content {
  z-index: 2;
  opacity: 1;
  transition: all .35s;
}

